.chart_occupation_trends{
	.chart_main {
		.line {
			fill: none;
			stroke-width: 1.3px;
		}
		.line--searchfade {
			stroke-width: 2px;
			stroke: gray !important;
			opacity: 0.3;
		}
		.line--fade {
			stroke-width: 2px;
			stroke: gray !important;
			opacity: 0.3;
		}
			
		.subgroup-container.fade,
		.subgroup-container.searchfade
		{
			.end_circle {
				opacity: 0.3;
				fill: gray!important;
				stroke: gray!important;
			}
		}

		.subgroup-container.hover,
		.subgroup-container.searchhover {
			.end_circle {
				stroke-width: 5px;
				fill: auto;
				stroke: auto;
			}
		}
		.line--hover {
			stroke-width: 5px;
		}
		.line--searchhover {
			stroke-width: 5px;
		}

		#inputBox {
			border-radius: 10px;
		}

		svg text {
			pointer-events: none;
		}

		.tooltip_y_first, .tooltip_y_last, .tooltip {
			font-size: 14px;
			font-weight: bold;
		}

		.axis_x, .axis_y {
			font-size: 14px;
		}
	}

	.sidepanel {


		.sidepanel_chart1 {
			min-height: 1px;
			width: 100%;

			svg {
				.label_value {
					font-weight: bold;
				}
				.line {
					fill: none;
					stroke-width: 2px;
				}
				.line_area {
					fill: #548235;
					opacity: 0.2;
				}
			}
		}

		.tooltip_y_first,.tooltip_y_last {
			pointer-events: none;
		}

		.tooltip {
			pointer-events: none;
			text {
				fill: darken(#838383, 30%);
				alignment-baseline: middle;
				font-size: 1.1rem;
			}
			text.smaller {
				font-size: 0.4rem;
			}
		}


		.charts_side_by_side {
			margin-top: 3rem;
			& > * {
				width: 50%;
				min-height: 1px;
				box-sizing: border-box;
			}
			& > * {
				padding-left: 0.2rem;
				float: left;
			}
			& > *:first-child {
				padding-left: 0;
				padding-right: 0.2rem;
			}
			.cross_chart svg {
				margin-left: -20px;
			}
		}
		@media screen and (max-width: 1281px) {
			.charts_side_by_side {
				.cross_chart svg {
					margin-left: 50%;
					transform: translate(-50%,0);
				}
				& > * {
					width: 100%;
					float: none;
				}
			}
		}
	}


}
