.chart_pop_earnings_map {
	.chart_main {
		margin-left: 2vw;
		display: inline-block;

		.cross_chart {
			padding: 0.5rem;
			padding-bottom: 0;
			&:hover {
				outline: 2px solid #1d9db9;
			}
			cursor: pointer;
			margin-bottom: 2rem;

			h3.all_occ {
				font-size: 1.3rem;
				margin-bottom: 1rem;
				margin-top: 0rem;
			}
		}

		h3 {
			padding-left: 0;
			margin-top: 2rem;
			margin-bottom: -1.5rem;
		}
	}

	.chart_main svg {
		position: static;
	}


	.subcharts {


		h3 {
			font-size: 15px;
			line-height: 15px;
			height: 45px;
			// font-weight: normal;
			padding: 0 10px;
			margin-bottom: 10px;
		}

		.subchart {
			width: 16.666%;
			// height: 25vw;
			max-height: 300px;
			padding-top: 16.666%;
			position: relative;

			margin-bottom: 5rem;
			float: left;
			text-align: center;

			.subchart__inner {
				padding-top: 0.5rem;
				position: absolute;
				top: 0;
				left: 0;
				cursor: pointer;
				&:hover {
					outline: 2px solid #1d9db9;
				}
			}

			svg {
				display: inline-block;
			}

		}
	}

	.sidepanel {
		.cross_chart {
			text-align: center;
			.cross_chart_inner {
				display: inline-block;
			}
		}
	}

}
