.chart_wage_gap{
	.chart_vertiline {
		svg text {
			pointer-events: none;
		}

		.circle {
			opacity: 0.15;
		}

		.line {
			opacity: 0.3;
		}
		.line--hover {
			opacity: 0.6;
		}
		.line--searchhover {
			opacity: 0.6;
		}

		.circle--hover {
			opacity: 1;
		}
		.circle--searchhover {
			opacity: 1;
		}

		.tooltip {
			opacity: 0;
		}
		.tooltip--hover {
			opacity: 1;
		}
		.axis_lefty {
			.tick text {
				font-size: 12px;
			}
		}
	}

	.tooltip_container {
		position: absolute;
		z-index: 2;
		top: 0;
		left: 0;
		pointer-events: none;

		.tooltip, .tooltip_always_vis {
			position: absolute;
			width: 300px;
			transform: translate(-50%, 0);
			text-align: center;
			span {
				display: block;
				width: 100%;
			}
		}
	}

	.sidepanel {
		.sidepanel_chart1,
		.sidepanel_chart2 {
			width: 100%;
			height: 400px;
			position: relative;
		}

		.sidepanel_chart1 {
			margin-bottom: 8rem;
			.tooltip {
				font-size: 0.9rem;
			}
		}
	}
}
