html, body {
	font-size: 16px;
}

.bold {
	font-weight: bold;
}

h2 {
	font-size: 1.4rem;
	color: #7f7f7f;
}
h3 {
	font-size: 1.3rem;
	font-weight: normal;
	color: #7f7f7f;
}


.page {
	width: 100%;
	position: relative;
	padding-left: 3.5rem;
	padding-right: 3.5rem;
	margin-top: 3rem; /* header */
	padding-top: 1rem; /* header */
	box-sizing: border-box;
	min-height: calc(100vh - 3rem);

	.chart-container {
		max-width: 1340px;
		padding: 2rem;
		position: relative;

		.chart_main {
			width: 100%;
		}
	}
}

.chart-tabs {
	width: 100%;
	background: #e5f4f8;
	color: #1d9db9;
	height: 3rem;
	z-index: 10;
	position: absolute;
	top: 0;
	left: 0;


	padding-left: 2rem;
	padding-right: 2rem;

	.chart-tabs-limiter {
		max-width: 900px;
		display: -webkit-box;
		display: -ms-flexbox;
		display: flex;
		-ms-flex-wrap: wrap;
		flex-wrap: wrap;
		justify-content: space-between;
	}


	.chart-tab {
		padding: 0.5rem 2rem;
		text-align: center;
		vertical-align: middle;
		line-height: 2rem;
		cursor: pointer;

		&.selected {
			font-weight: bold;
			border-bottom: 3px solid #1d9db9;
		}

		.metric-title {
			height: 2rem;
			line-height: 2rem;
		}
	}

	.metric-value, .metric-title {
		width: 100%;
	}
}

.input_c {
	position: absolute;
	top: 2rem;
	left: 2rem;
	z-index: 2;

	.bi {
		transform: scale(1.5);
	}

	input {
		margin-left: 10px;
		border-radius: 5px;
		border: 1px solid lighten(#838383, 15%);
		outline: none;
	}
}

.chart-pointer-event-catcher {
	position: fixed;
	top: 0;
	right: 0;
	bottom: 0;
	left: 0;
	opacity: 0;
	background: rgb(0,0,0);
	pointer-events: none;
	z-index: 3;
	&.open {
		pointer-events: all;
	}
}



.cross_chart {
	position: relative;

	.bg {
		z-index: -1;
		background: white;
		display: none;
		border: 1px solid #ccc;
		position: absolute;
		top: 0;
		left: 10px;
		width: 320px;
		height: 225px;
	}

	svg {
		margin-top: 10px;
		margin-left: 10px;

	}

	.legend {
		text {
			fill: #7f7f7f;
			// font-size: 1rem;
		}
		rect {
			fill:white;
		}
	}
}

.sidepanel {
	background: #f2f2f2;
	position: fixed;
	box-sizing: border-box;
	right: 0;
	top: 0;
	// width: 550px;
	width: 600px;
	// max-width: 900px;
	// min-width: 550px;
	height: calc(100vh);
	z-index: 5;
	display: none;
	padding: 2rem 1.5rem;
	padding-top: 5rem;
	overflow-y: scroll;
	overflow-x: hidden;

	.close {
		position: absolute;
		right: 0.6rem;
		top: 3.3rem;
		font-size: 1.5rem;
		font-weight: bold;
		color: #7f7f7f;
		cursor: pointer;
	}

	&.open{
		display: block;
	}


	table {
		width: 100%;
		border: 1px solid gray;
		margin: 0.1rem 0.5rem;
		box-sizing: border-box;
		font-size: 0.8rem;
		// float: left;

		tr {
			border-bottom: 1px solid gray;
		}
		tr:last-child {
			border-bottom: none;
		}

		tr td:last-child {
			border-right: none;
		}

		tr td {
			border-right: 1px solid gray;
		}
	}
}

.chart_main {
	svg {
		z-index: 1;
		position: absolute;
		top: 0;
		left: 0;
	}
}

.cross_line {
	stroke: #ccc;
}

.cross_legend_dom {
	color: #7f7f7f;

	.legend_item {
		display: inline-block;
		margin-left: 0.3rem;
		span {
			position: relative;
			top: -2px;
		}
	}

	.legend_rect {
		margin-left: 0.2rem;
		width: 15px;
		height: 15px;
		background: black;
		display: inline-block;
		&--us {
			background: #a6a6a6;
		}
		&--state {
			background: #ffd966;
		}
		&--city {
			background: #119eb9;
		}
	}

	&--hor {
		margin-top: 2rem;
		float: left;
		// position: absolute;
		// right: 0;
		// width: 150px;
		.legend_item {
			display: block;
		}
	}
}

.clearfix {
	clear: both;
}

.sidepanel_tables {
	h3 {
		margin-top: 0;
		margin-left: 0.5rem;
		font-size: 1.3rem;
	}
	table {
		margin-bottom: 2rem;
		color: darken(#7f7f7f, 20%);
		font-size: 0.75rem;
		td {
			padding: 0.2rem;
		}
	}
}
